<template>
  <div id="app">
    <product-list :products="products"></product-list>
  </div>
</template>

<script>
import productList from './components/product-list.vue'

export default {
  name: 'App',
  components: {
    'product-list': productList
  },
    data: function() {
      return {
        // 商品リスト
        products: [],
      // エラーの有無
      isError: false,
      // メッセージ
      message: ''
      }
  },
  // ライフサイクルフック
  created: function() {
  // 非同期通信でJSONを読み込む
  $.ajax({
    url : '/sampleweb/products',  // 通信先URL
    type: 'GET',              // 使用するHTTPメソッド
    dataType: 'json',        // レスポンスのデータタイプ
  })
  .done(function(data) {
    this.products = data;
  }.bind(this))
  .fail(function() {
    this.isError = true;
    this.message = '商品リストの読み込みに失敗しました。';
  }.bind(this));
  },
}
</script>

<style>
body {
	background: linear-gradient(#a3d6cc, #ffffff) fixed;
}
</style>
